.question {
  &__items {
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 4px;
  }

  &__item {
    border-radius: 4px;
    background: var(--white);
    padding: 24px;
    display: flex;
    gap: 24px;

    svg {
      flex-shrink: 0;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    gap: 12px;
  }
}
