@use "@/styles/mixins";

.categories {
  opacity: 0.5;
  display: flex;
  flex-wrap: wrap;
  column-gap: 16px;
  row-gap: 6px;

  @include mixins.lg {
    gap: 7px;
    flex-direction: row;
  }
}

.category {
  display: flex;
  gap: 7px;

  &__bullet {
    display: none;

    @include mixins.lg {
      display: block;
    }

    &_first {
      @include mixins.lg {
        display: none;
      }
    }
  }
}
