@use "@/styles/mixins";

.dialog {
  border: 0;

  &::backdrop {
    background-color: var(--ghost-70);
  }

  &__element {
    border: 0;
    position: fixed;
    max-height: calc(100vh - 16px);
    height: fit-content;
    width: calc(100% - 16px);
    margin: 8px;
    top: 0;
    border-radius: 8px;
    border: 1px solid var(--white-30);
    background: linear-gradient(
      180deg,
      rgba(247, 247, 247, 0.8) 0%,
      rgba(247, 247, 247, 0.5) 25.18%
    );
    backdrop-filter: blur(16px);
    padding: 32px 24px;
    overflow-y: auto;
    left: 50%;
    transform: translateX(-50%);
    max-width: 840px;
    margin: 8px 0 0;

    & > * {
      position: relative;
    }

    @include mixins.md {
      max-height: calc(100vh - 96px);
      height: fit-content;
      width: calc(100% - 96px);
      margin: 48px;
      padding: 32px 52px 52px;
    }

    animation: fade-in 200ms linear reverse normal;

    .dialog[open] & {
      animation: fade-in 200ms linear forwards normal;
    }
  }

  &__header {
    display: flex;
    flex-direction: column;
    gap: 12px;
    padding: 32px 0;
    border-bottom: 1px solid var(--white-30);
  }

  &__close_wrapper {
    display: flex;
    justify-content: flex-end;
    position: sticky;
    top: 0;
    z-index: 10;
  }

  &__close {
    display: flex;
    height: 32px;
    width: 32px;
    align-items: center;
    justify-content: center;
  }

  .heading_s {
    margin-top: 24px;
    margin-bottom: 24px;
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 24px;

    & > * {
      display: flex;
      flex-direction: column;
      gap: 24px;
    }
  }
}

@keyframes dialog-slide-in {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}
