@use "@/styles/mixins";

.timeline {
  color: white;
  height: 100%;
  padding: 0 12px;
  grid-area: timeline;
  position: fixed;
  bottom: 0;
  max-height: calc(100vh - 186px);
  top: 165px;
  z-index: 20;
  transition: all 200ms ease-in;

  &.slideUp {
    top: 88px;

    @include mixins.lg {
      top: 116px;
    }
  }

  @include mixins.lg {
    max-height: calc(100vh - 120px);
    top: 116px;
    padding: 48px 54px 0;
  }

  &__expand {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    border-radius: 50%;
    border: 1px solid var(--white-60);
    box-sizing: border-box;
    margin-bottom: 24px;
    justify-self: center;

    @include mixins.lg {
      display: none;
    }
  }

  &__items {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    padding-left: 16px;

    @include mixins.lg {
      padding-left: 0;
    }

    li:last-of-type .stint__item::before {
      content: none;
    }
  }

  &__dynamic_lines {
    position: absolute;
    left: 35px;
    width: 2px;
    height: 100%;
    max-height: calc(100vh - 168px);

    @include mixins.lg {
      left: 61px;
    }
  }

  &__dynamic_line {
    width: 2px;
    height: calc(100% / 6 - 26px);
    background-color: white;
    transform-origin: top;
    top: 0;
    position: absolute;
    left: 0;
  }

  &__dynamic_0 {
    top: 22px;
  }

  &__dynamic_1 {
    top: calc(100% / 6 + 22px);
  }

  &__dynamic_2 {
    top: calc(100% / 6 * 2 + 22px);
  }

  &__dynamic_3 {
    top: calc(100% / 6 * 3 + 22px);
  }

  &__dynamic_4 {
    top: calc(100% / 6 * 4 + 22px);
  }

  &__dynamic_5 {
    top: calc(100% / 6 * 5 + 22px);
    display: none;
  }

  &__right {
    position: fixed;
    right: 0;
    top: 0;
    height: 100%;
    width: calc(100vw - 274px);
    display: none;

    @include mixins.lg {
      display: none;
    }
  }

  li {
    flex: 1 1 33.333%;
    display: flex;
    flex-direction: row;
  }

  .stint {
    &__item {
      transform: translate(0, 1px);
      width: max-content;
      display: flex;

      &::before {
        content: "";
        width: 2px;
        height: calc(100% - 28px);
        position: absolute;
        display: block;
        top: 22px;
        left: 7px;
        background-color: rgba(255, 255, 255, 0.2);
      }
    }

    &__inner {
      display: flex;
      align-items: center;
      height: fit-content;
      gap: 16px;
      position: relative;
    }

    &__dot {
      z-index: 1;
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      border: 1px solid var(--white);
      border-radius: 50%;
      width: 16px;
      height: 16px;
      flex-shrink: 0;

      span {
        width: 4px;
        height: 4px;
        border-radius: 50%;
        background-color: var(--white);
      }
    }

    &__label {
      opacity: 0;
      position: absolute;
      width: max-content;
      left: 30px;

      @include mixins.lg {
        opacity: 1;
        position: relative;
        width: 100%;
        left: 0;
      }
    }
  }
}
